:root {
  --page-bg-color: #f9f9fd;
  --primary-text-color: #212121;
  --card-set-gap: 20px;
  --card-shadow: 0px 2px 1px -1px rgba(0, 0, 0, .2), 0px 1px 1px 0px rgba(0, 0, 0, .14), 0px 1px 3px 0px rgba(0, 0, 0, .12);
}

* {
  box-sizing: border-box;
}

body {
  background-color: rgba(184, 207, 184, .08);
}

.search-form {
  text-align: center;
  background: gray;
  margin: 10px auto;
  padding: 20px;
}

.search-form input {
  height: 30px;
  display: inline-block;
}

.search-form input :hover, :focus {
  outline: none;
}

button {
  height: 30px;
  display: inline-block;
}

.gallery {
  margin: 0;
  margin-left: calc(-1 * var(--card-set-gap) );
  margin-top: calc(-1 * var(--card-set-gap) );
  flex-wrap: wrap;
  margin-bottom: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

.photo-card {
  flex-basis: calc(100% / 4 - var(--card-set-gap) );
  margin-left: var(--card-set-gap);
  margin-top: var(--card-set-gap);
  box-shadow: var(--card-shadow);
  background-color: rgba(255, 255, 255, .75);
  border: 2px #000;
  border-radius: 4px;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  font-size: small;
  display: flex;
  overflow: hidden;
}

.photo-card img {
  height: 150px;
  width: 100%;
  object-fit: cover;
}

.info {
  justify-content: space-between;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin-bottom: 0;
  display: flex;
}

.hidden {
  display: none;
}

